import React, {useEffect, useState} from "react";

export default function Payment(){

    const [isScriptLoaded, setScriptLoaded] = useState(false);

    useEffect(() => {
        const jqueryScript = document.createElement('script');
        jqueryScript.src = "https://code.jquery.com/jquery-3.6.0.min.js";
        jqueryScript.async = false; //  
        document.body.appendChild(jqueryScript);

        const iamportScript = document.createElement('script');
        iamportScript.src = "https://cdn.iamport.kr/js/iamport.payment-1.1.8.js";
        iamportScript.async = false;

        iamportScript.onload = () => {
            setScriptLoaded(true);
            const IMP = window.IMP;
            IMP.init('imp20007130'); //   " " 
        };

        document.body.appendChild(iamportScript);

        return () => {
            document.body.removeChild(jqueryScript);
            document.body.removeChild(iamportScript);
        };
    }, []);


    const onPayment = () => {
        console.log("onPayment  .");
        if (!isScriptLoaded) {
            alert("    .    .");
            return;
        }
        const IMP = window.IMP;

        IMP.request_pay({
            pg: 'html5_inicis',
            pay_method: 'card',
            merchant_uid: 'merchant_' + new Date().getTime(),
            name: ' ',
            amount: 100,
            buyer_email: 'jwoo3460@naver.com',
            buyer_name: '',
            buyer_tel: '01091753460',
            buyer_addr: '  ',
            buyer_postcode: '123-456',
            m_redirect_url: 'http://localhost:8080/payment/callback'
        }, function (rsp) {
            if (rsp.success) {
                alert(" ");
            } else {
                alert(" ");
            }
        });
    }

    return (
        <button onClick={onPayment}> </button>
    );
}